@import "../dropdown_list/navbar_dropdown_list.css";

.navbar-container {
    display: flex;
    background-color: var(--default-bg-color);
    min-height: 80px;
    max-height: 80px;
    color: white;
    font-family: var(--default-font-family);
    justify-content: space-between;
}

.left-container {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.right-container {
    display: flex;
    flex-direction: row-reverse;
}

.login-button {
    color: white;
    background-color: var(--default-bg-color);
    border: none;
    width: 100px;
    font-size: 20px;
    font-family: var(--default-font-family);
    font-weight: bold;
}

.login-button:hover {
    background-color: white;
    color: var(--default-bg-color);
}
