.body-container {
    display: flex; 
    flex-direction: column;
}

.region-and-app-id-container {
    display: flex;
    align-items: center;
    margin: 3px 10%;
    margin-bottom: 10px;
}

.region-and-app-id-container input {
    margin-left: 10px;
    font-size: 20px;
    width: 100%;
    height: 3em;
    border-radius: 8px;
    outline: None;
    border: 2px solid black;
    padding-left: 15px;
    font-family: var(--default-font-family);
    font-size: 15px; 
}

.region-select { 
    margin: 0% 10%;
}

.description-container {
    min-height: 150px;
    max-height: 200px;
    margin: 30px 10%;
    display: flex;
    flex-direction: column;
    background-color: white;
    border: 2px solid black;
    border-radius: 8px;
}

.description-title-text, 
.description-body-text {
    font-family: var(--default-font-family);
    border: 2px solid black;
    border-radius: 8px;
}
 
.description-title-text { 
    position: relative;
    display: flex;
    left: 2%;
    top: -1em;
    min-height: 2em;
    max-width: 90%;
    align-items: center;
    background-color: var(--default-bg-color);
    padding: 0px 10px;
    font-size: 18px;
    color: white;
}

.description-body-text { 
    font-size: 12px;
    margin: 5px 4%;
    overflow-y: scroll;
    padding: 10px 10px;
}

.footer-container { 
    display: flex;
    flex-direction: column;
    margin: 0 10%;
    margin-bottom: 2%;
    justify-content: space-between;
}

.submission-container { 
    display: flex;
    height: 4.5rem;
    border-radius: 12px;
    font-family: var(--default-font-family);
}

.result-container { 
    width: 80%;
    height: 100%;
    color: black;
    border-radius: 5px;
    background-color: white;
    display: flex;
    align-items: center;
    padding: 0px 10px;
    justify-content: space-between;
}

.form-submission-left-container { 
    display: flex; 
    width: 100%;
}

.form-submission-right-container { 
    display: flex;
    justify-content: space-between;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(5px);
  }
}

.error-message { 
    color: red;
    font-weight: bold;
    transform: translateX(0);
    animation: shake 0.5s 2; 
}

.atar-result {

}

.button-container {
    display: flex;
    width: 100%;
}

.submit-button,
.clear-button,
.cat-button {
    margin: 0px 10px;
    width: 6rem;
    height: 100%;
    font-size: 15px;
    background-color: var(--default-bg-color); 
    font-family: var(--default-font-family);
    color: white;
    border: none;
    border-radius: 8px;
    transition: 0.3s;
    font-weight: bold;
}

.cat-button { 
    margin: 0;
    position: relative;
    left: -1%;
    height: 100%;
    width: 5em;
}

.submit-button:hover, 
.clear-button:hover,
.cat-button:hover
{
    scale: 1.2;
}

.note-container {
    font-family: var(--default-font-family);
    border: 2px solid black;
    border-radius: 8px;
    padding: 10px; font-size: 15px;
    margin: 10px 10%;
    height: 5.5em;
}
