.pdf-uploader-container {
}

.file-upload-box {
    background-color: white;
    font-family: var(--default-font-family);
    font-size: 15px;
    margin: 10px 5%;
    border: 2px dashed black;
    border-radius: 4px;
    padding: 0.8em;
    text-align: center;
    cursor: pointer;
    transition: 0.5s;
}

.file-upload-box > img { 
    display: none;
}

.file-upload-box:hover {
    transform: scale(1.1);
}

.file-upload-box:hover img {
    display: inline;
}

.navigation-container { 
    display: flex;
    justify-content: space-between;
    margin: 20px 5%;
}

.pdf-navigation-container { 
    width: 61%;
}

.pdf-selector-container{ 
    width: 60%;
}

.page-navigation-container {
    display: flex;
    width: 40%;
    align-items: center;
    justify-content: space-between;
}

.pdf-selector, 
.pdf-navigation-container > select {
    background-color: white;
    outline: none;
    border: 2px solid black;
    padding: 0.5em;
    border-radius: 8px;
    margin: 0px 8px;
    overflow: hidden;
    max-width: 60%;
}

.page-navigation-container > button {
    background-color: white;
    outline: none;
    border: 2px solid black;
    padding: 0.5em;
    border-radius: 8px;
    min-width: 80px;
}

.page-navigation-container > h3 {
    font-size: 13px;
    margin: 0px 10px;
}

.pdf-display-container { 
    display: flex; 
    justify-content: center;
    background-color: #C7B7A3;
    border: 2px solid black;
    margin: 8px 5%;
    overflow-x: scroll;
    overflow-y: scroll;
    height: 1000px;
}
