.subject-table {
    font-family: var(--default-font-family);
    color: white;
    display: flex;
    flex-direction: column;
    margin: 10px 10%;
    border-radius: 12px;
}

.table-header {
    text-align: center;
    display: flex;
    font-weight: bold;
    max-width: 100%;
    /* box-sizing: border-box; */
}

.header-item { 
    background-color: var(--default-bg-color);
    border: 2px solid black;
    border-right: None;
    padding: 1.3em 0.5em;
    margin: 0px; 
    font-size: 15px;
}

.table-body {
    background-color: white;
    color: black;
    border-radius: 0px 0px 12px 12px;
}
